export const getAddress = async (lat, long) => {
  return await (await fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${long}&key=${process.env.VUE_APP_GOOGLE_MAP_KEY}`)).json()
}

export const ipLookUp = async () => {
  try {
    const res = await (await fetch('http://ip-api.com/json')).json()
    console.log(res);
    return getAddress(res.lat, res.lon)
  }
  catch (e) {
    console.log(e)
    return null
  }
}

export default () => {
  if ("geolocation" in navigator) {
    navigator.geolocation.getCurrentPosition(
      (position) => {
        console.log('latitude', position.coords.latitude, 'longitude', position.coords.longitude);
        return getAddress(position.coords.latitude, position.coords.longitude)
      },
      (error_message) => {
        console.log('An error has occured while retrieving location', error_message)
        return ipLookUp()
      }
    );
  } else {
    console.log('geolocation is not enabled on this browser')
    return ipLookUp()
  }
}